.questionbody{
    display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
  
  label {
    font-size: 18px;
    font-weight: bold;
    margin-right: 10px;
  }
  
  input[type="range"] {
    width: 300px;
    margin-bottom: 20px;
  }
  
  span {
    display: inline-block;
    margin-left: 10px;
    font-size: 18px;
    font-weight: bold;
  }
  
  button[type="submit"] {
    background-color: #007bff;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    margin-top: 30px;
    cursor: pointer;
  }
  
  button[type="submit"]:hover {
    background-color: #0069d9;
  }