label {
    display: block;
    margin-bottom: 5px;
  }
  
  /* Style the form inputs */
  input[type="text"],
  input[type="email"],
  input[type="password"],
  select {
    display: block;
    width: 600px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  
  /* Style the submit button */
  button[type="submit"] {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button[type="submit"]:hover {
    background-color: #45a049;
  }
  
  /* Style the Login/Register toggle button */
  button{
    background-color: #ddd;
    color: #333;
    /* padding: 10px 20px; */
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #bbb;
  }
  
  /* Style the page container */
  .logindiv {
    margin:  auto;
    padding: 20px;
  } 